<template>
    <div class="yg_storage_try_page">
        <p>
            
        </p>
        <p>
            <input type="text" name="" id="" v-model="storeValue">
        </p>
        
        <button @click="onLocalSave">LocalStorage</button>
        <button @click="onSessionSave">SessionStorage</button>
        <button @click="onClear"></button>
        <p>localStorage{{ getLocalStorage() }}</p>
        <p>sessionStorage{{ getSessionStorage() }}</p>
    </div>
</template>


<script>
// SessionStorageHTML5
// VueSessionStorage

// LocalStorageHTML5
// VueLocalStorage

// 5MB
// 

const MY_LOCAL_STORAGE_KEY = 'ZHQ_LOCAL_KEY'
const MY_SESSION_STORAGE_KEY = 'ZHQ_SESSION_KEY'

export default {
    name:"StoragePage",
    data(){
        return {
            storeValue: ''
        }
    },
    methods: {
        onLocalSave() {
            if (this.storeValue.length === 0) {
                alert('')
                return
            }
            localStorage.setItem(MY_LOCAL_STORAGE_KEY, this.storeValue)
            this.storeValue = ''
        },
        onSessionSave() {
            if (this.storeValue.length === 0) {
                alert('')
                return
            }
            sessionStorage.setItem(MY_SESSION_STORAGE_KEY, this.storeValue)
            this.storeValue = ''
        },
        onClear() {
            localStorage.removeItem(MY_LOCAL_STORAGE_KEY)
            sessionStorage.removeItem(MY_SESSION_STORAGE_KEY)
            this.$forceUpdate()
        },
        getLocalStorage(){
            return localStorage.getItem(MY_LOCAL_STORAGE_KEY)
        },
        getSessionStorage() {
            return sessionStorage.getItem(MY_SESSION_STORAGE_KEY)
        }
    },
    watch: {

    },
    computed: {
        
    }
}
</script>

<style scoped>

.yg_storage_try_page {
    padding: 20px;
}

</style>